@use "global" as *;

.price {
  background-color: #DDF0F1;
  // position: relative;
  padding-top: rem(80);
  padding-bottom: rem(80);
  @include mq("md") {
    padding-top: rem(103);
    padding-bottom: rem(100);
  }
}

.price__header {
  text-align: center;
}

.price__wrapper {
  margin-top: rem(30);
  display: flex;
  flex-direction: column;
  gap: rem(38);
  justify-content: center;
  max-width: rem(600);
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  @include mq("md") {
    margin-top: rem(40);
    flex-direction: row-reverse;
    gap: rem(93);
    max-width: rem(1080);
  }
}

.price__img,
.price__contents {
  @include mq("md") {
    max-width: rem(492);
    width: 100%;
  }
}

.price__img {
  aspect-ratio: 345 / 227;
  height: 100%;
  @include mq("md") {
    aspect-ratio: 492 / 746;
  }
}

.price__img img {
  height: 100%;
  width: 100%;
  object-fit: cover;
}

.price__link {
  text-align: center;
  margin-top: rem(40);
  @include mq("md") {
    margin-top: rem(80);
  }
}